*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.minesweeper{
  width: 100%;
  background-color: rgb(187, 135, 76);
  justify-content: center;
  display: flex;
  align-items: center;
}

.board{
  margin-top: 100px;
  margin-bottom: 300px;
  text-align: center;
  border-radius: 20px;
  background-color: rgb(255, 94, 0);
  border: solid 1px;
}
.numberNeed{
  margin-right: 10px;
  text-align: end;
  height: 0px;
  font-weight: bold;
  font-size: 1.3em;
}

.back{
  cursor: pointer;
  margin-left: 10px;
  text-align: start;
  height: 0px;
  font-weight: bold;
  font-size: 1.3em;
}
.timer{
  font-weight: bold;
  font-size: 1.5em;
  display: flex;
  justify-content: center;
  margin: 10px;
}
img{
  width: 30px;
  height: 30px;
  position: static;
  margin-right: 10px;
}
.all-row{
  margin: 15px;
}
.row_digit{
  justify-content: center;
  align-items: cneter;
  display: flex;
}

.digit{
  border-radius: 5px;
  text-align: center;
  width: 30px;
  height: 30px;
  margin: 1px;
  background-color: red;
  border: solid 2px rgb(65, 59, 59);
  cursor: pointer;
  font-weight: bold;
}

.digit:hover{
  opacity: 0.7;
  background-color: aqua;
}

.open{
  
  border-radius: 5px;
  text-align: center;
  width: 30px;
  height: 30px;
  margin: 1px;
  background-color: rgb(182, 177, 177);
  border: solid 2px rgb(65, 59, 59);
  cursor: pointer;
  font-weight: bold;
}
.col-1{
  color: black;
  background-color: darkblue;
}
.col-2{
  color: black;
  background-color: yellow;
}
.col1{
  color: blue;
}
.col2{
  color: indigo;
}
.col3{
  color: saddlebrown;
}
.col4{
  color: rgb(117, 117, 3);
}
.col5{
  color: rgb(97, 80, 212);
}
.col6{
  color: rgb(94, 91, 91);
}
.col7{
  color: rgb(37, 84, 170);
}
.col8{
  color: black;
}

.col9{
  background-color: rgb(0, 255, 0);
}

.buttons{
  margin: 15px;
  display: flex;
  justify-content: space-evenly;
}
.smallbuttons{
  align-items: center;
  flex-direction: column;
  display: flex;
  margin: 20px;
  margin-left: 50px;
  margin-right: 50px;
}
.reset{
  margin-top: 10px;
  font-size: x-large;
  text-align: center;
  width: 100px;
  height: 40px;
  background-color: saddlebrown;
  cursor: pointer;
  border-radius: 10px;
  border: solid 0.5px ;
}
.reset:hover{
  opacity: 0.7;
  background-color: aqua;
  
}

.cong{
  margin-top: 10px;
  font-size: x-large;
  text-align: center;
  border-radius: 10px;
  font-weight: bold;
  color: rgb(0, 255, 0);
}
.gameover{
  margin-top: 10px;
  font-size: x-large;
  text-align: center;
  border-radius: 10px;
  font-weight: bold;
  color: darkblue;
}

.forinput{
  width: 300px;
  justify-content: space-around;
  display: flex;
}

.forinput input{
  font-size: larger;
  text-align: center;
  width :40px;
  height: 40px;
  border-radius: 20px;
  border: solid 1px;
  margin: 10px;
}

button{
  margin: 10px;
  width: 80px;
  height: 40px;
  font-size: larger;
  font-weight: bold;
  background-color: white;
  border-radius: 10px;
  border: solid 2px;
}

.labels{
  color: white;
}

.welcome{
  margin: 10px;
  color: white;
  font-size: x-large;
  font-weight: bold;
  font-family: Georgia, 'Times New Roman', Times, serif
}

@media only screen and (max-width: 600px) {
  .minesweeper{
    justify-content: start;
  }
  .board{
    margin-left: 60px;
  }
}